.Navbar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    height: 75px;
    background-color: rgb(55, 55, 117);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0 25px;
    font-size: 25px;
    font-weight: bold;
}

.Navbar a,
.User a {
    text-decoration: none;
    color: white;
}

.Navbar a:visited,
.User a:visited {
    color: white;
}

.User {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.User p {
    margin-right: 50px;
    color: white;
}

.User p:hover {
    cursor: pointer;
}

.User a {
    margin-right: 50px;
}

.Cart::after {
    content: 'Added to cart';
    position: absolute;
    display: block;
    background-color: rgb(68, 161, 68);
    text-align: center;
    font-weight: bold;
    border-radius: 20px;
    padding-top: 15px;
    width: 200px;
    height: 0px;
    opacity: 0;
    top: 90px;
    right: 25px;
}

.Add:after {
    -webkit-animation: added 5s;
            animation: added 5s;
}

@-webkit-keyframes added {
    0% {
        height: 0px;
        opacity: 0;
    }
    15% {
        height: 50px;
        opacity: 1;
    }
    90%{
        height: 50px;
        opacity: 1;
    }
    95% {
        height: 0;
        opacity: 0;
    }
}

@keyframes added {
    0% {
        height: 0px;
        opacity: 0;
    }
    15% {
        height: 50px;
        opacity: 1;
    }
    90%{
        height: 50px;
        opacity: 1;
    }
    95% {
        height: 0;
        opacity: 0;
    }
}

.Burger {
    display: none;
}

.Burger::after {
    content: 'Added to cart';
    position: absolute;
    display: block;
    background-color: rgb(68, 161, 68);
    text-align: center;
    font-weight: bold;
    border-radius: 20px;
    padding-top: 15px;
    width: 200px;
    height: 0px;
    opacity: 0;
    top: 90px;
    right: 25px;
    color: white;
}

.Add:after {
    -webkit-animation: added 5s;
            animation: added 5s;
}

.Bar1,
.Bar2,
.Bar3 {
    width: 35px;
    height: 5px;
    background-color: white;
    margin: 6px 0;
    -webkit-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s;
}

.SideNav {
    position: fixed;
    height: 100%;
    width: 0px;
    opacity: 0;
    z-index: -10;
    top: 0;
    right: 0;
    background-color: black;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    color: white;
    -webkit-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s;
    font-size: 22px;
}

.SideNav p.Close {
    text-align: right;
    margin-right: 25px;
    font-weight: bold;
}

.Clicked {
    width: 350px;
    opacity: 1;
    z-index: 10;
}

.SideNav a,
.SideNav p {
    margin: 25px auto;
}

@media screen and (max-width: 830px) {
    .User {
        display: none;
    }

    .Burger {
        display: inline-block;
    }
}


