.Cart h1 {
    margin-top: 25px;
    text-align: center;
}

.Bottom {
    width: 65%;
    margin: 50px auto;
}

.Bottom p {
    font-size: 28px;
    font-weight: bold;
}

.Bottom button {
    width: 150px;
    height: 75px;
    background-color: #eee;
    border: 1px solid black;
    font-weight: bold;
    font-size: 25px;
    outline: none;
}

.Bottom button:hover {
    border: 1px solid white;
    background-color: rgb(55, 55, 117);
    color: white;
}

.Bottom button:active {
    -webkit-transform: scale(0.9);
        -ms-transform: scale(0.9);
            transform: scale(0.9);
}

@media screen and (max-width: 1200px) {
    .Bottom {
        width: 85%;
    }
}

@media screen and (max-width: 600px) {
    .Bottom {
        width: 95%;
    }
}


