.Checkout h1 {
    margin-top: 25px;
    text-align: center;
}

.Title {
    text-align: center;
    margin-top: 50px;
    font-size: 45px;
}

.Bottom {
    width: 65%;
    margin: 50px auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.Bottom button {
    width: 150px;
    height: 75px;
    background-color: #eee;
    border: 1px solid black;
    font-weight: bold;
    font-size: 25px;
    outline: none;
    margin: 50px auto 0 auto;
}

.Bottom button:hover {
    border: 1px solid white;
    background-color: rgb(55, 55, 117);
    color: white;
}

.Bottom button:active {
    -webkit-transform: scale(0.9);
        -ms-transform: scale(0.9);
            transform: scale(0.9);
}

.Bottom button:disabled {
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    border: 1px solid rgba(0, 0, 0, 0.3);
    pointer-events: none;
}

.Inputs,
.Charges {
    height: 400px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
    box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
}

.Inputs {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    padding-left: 25px;
    padding-top: 20px;
    -ms-flex-preferred-size: 30%;
        flex-basis: 30%;
}

.Inputs p {
    font-size: 25px;
    font-weight: bold;
    text-align: center;
    margin-top: 0;
}

.Charges {
    -ms-flex-preferred-size: 65%;
        flex-basis: 65%;
    text-align: center;
    padding-top: 50px;
}

.Charges p {
    font-weight: bold;
    font-size: 28px;
}

.Charges p.Total {
    font-size: 40px;
}

.Charges hr {
    border: 0;
    height: 1px;
    width: 50%;
    background-image: -webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 0)), color-stop(rgba(0, 0, 0, 0.75)), to(rgba(0, 0, 0, 0)));
    background-image: -webkit-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
    background-image: -o-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
}

.Inputs input,
.Inputs select {
    width: 75%;
    height: 30px;
    border: 1px solid black;
    outline: none;
    padding: 5px;
    margin-bottom: 10px;
    padding-left: 10px;
    font-size: 15px;
}

.Inputs select {
    width: 225px;
    height: 35px;
}

.Inputs select.State {
    width: 75px;
}

input[type=number] {
    width: 150px;
}

.Inputs input:focus,
.Inputs select:focus {
    border: 1px solid rgb(55, 55, 117);
}

@media screen and (max-width: 1200px) {
    .Bottom {
        width: 85%;
    }

    .Inputs select {
        width: 200px;
    }
}

@media screen and (max-width: 920px) {
    .Bottom {
        -ms-flex-flow: column;
            flex-flow: column;
    }

    .Charges {
        margin-top: 25px;
    }
}

@media screen and (max-width: 600px) {
    .Bottom {
        width: 100%;
    }

    .Charges p {
        font-weight: bold;
        font-size: 20px;
    }
    
    .Charges p.Total {
        font-size: 32px;
    }
}


