.mainContent {
    width: 80%;
    height: 100%;
    margin: 40px auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.Apparel {
    -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
    height: 500px;
}

.Games {
    -ms-flex-preferred-size: 50%;
        flex-basis: 50%;
    height: 350px;
}

.Collectables {
    -ms-flex-preferred-size: 50%;
        flex-basis: 50%;
    height: 350px;
}

.Apparel,
.Games,
.Collectables {
    position: relative;
}

.Overlay {
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-size: 75px;
    font-weight: bold;
    color: white;
    opacity: 0;
    top: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    -webkit-transition: all linear 0.2s;
    -o-transition: all linear 0.2s;
    transition: all linear 0.2s;
}

.Apparel:hover .Overlay,
.Games:hover .Overlay,
.Collectables:hover .Overlay {
    width: 100%;
    opacity: 1;
}

.Apparel img,
.Games img,
.Collectables img {
    width: 100%;
    height: 100%;
}


@media screen and (max-width: 768px) {
    .mainContent {
         -ms-flex-flow: column;
             flex-flow: column;
    }

    .Apparel,
    .Games,
    .Collectables {
        height: 350px;
        -ms-flex-preferred-size: 100%;
            flex-basis: 100%;
    }
}

@media screen and (max-width: 500px) {
    .mainContent {
        width: 95%;
    }
}



